import React, { Component } from 'react';
import axios from 'axios';

class Index extends Component {
  static async getInitialProps() {
    const courses = await axios.get(`https://lexcampus.on-its-way.com/wp-json/ldlms/v1/sfwd-courses/`);
    return { courses };
  }

  render() {
    return (
      <div>
        <h1>{this.props.courses.title.rendered}</h1>
      </div>
    )
  }
}

export default Index;
